h1{
    margin-left: 30px;
}

h2{
    color: black;
}

h3{
    color: black;
}

.list{

    display: flex;
    margin: 30px;
    padding: 10px;
    align-items: center;
    justify-content: center;
}

.product{

    margin: 20px;
    padding: 20px;
    justify-content: center;
    align-items: center;
    align-self: center;
}
.product img {

    height: auto;
    width: 200px;
    margin-bottom: 10px;
    position: relative;
    left: 15px;
}

.product h2{

    position: relative;
    left: 40px;
    opacity: 0.9;
    font-weight :400;
}

.product h3{

    position: relative;
    left: -20px;
    font-size: 18px;
    opacity: 0.7;
    font-weight: 400;
}

.product h4{
    position: relative;
    top: -40px;
    left: -18px;
    opacity: 0.8;
    font-weight: 400;
}

.lm{


    margin: 10px;
    padding: 10px;
    background-color: blue;
    border: none;
    border-radius: 20px;
    color: white;
    height: 40px;
    width: 150px;
    cursor: pointer;
    font-size: 18px;

}

.bn{

    text-decoration: none;
    border: none;
    background-color: transparent;
    color: blue;
    cursor: pointer;
    font-size: 18px;
}

.bcomponent{

    position: relative;
    top: -50px;
    left: -20px;
}